.login-page {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-title {
    padding: 4px 16px;
    width: 80px;
}

.sub-form-part {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
}

.form-part {
    border: 2px solid rgb(16, 180, 112);
    padding: 20px;
    border-radius: 5px;
}

.submit-part {
    display: flex;
    justify-content: center;
    padding: 20px 0;
}